'use client';

import { useState, useEffect } from 'react';
import {
  Box, Container, Typography, Button, Grid, Card, CardContent, AppBar, Toolbar,
  IconButton, Tabs, Tab, Table, TableBody, TableCell, TableContainer, TableHead,
  TableRow, Paper, Chip, Dialog, DialogTitle, DialogContent, DialogActions,
  TextField, FormControl, InputLabel, Select, MenuItem, Alert
} from '@mui/material';
import {
  Lock as LockIcon, Folder as FolderIcon, Share as ShareIcon, Upload as UploadIcon,
  Settings as SettingsIcon, Search as SearchIcon, FilterList as FilterIcon,
  PersonAdd as PersonAddIcon, Security as SecurityIcon
} from '@mui/icons-material';
import axios from 'axios';

interface Asset {
  id: string;
  name: string;
  type: string;
  size: number;
  createdAt: string;
  encrypted: boolean;
  shared: boolean;
}

interface AccessRule {
  id: string;
  assetId: string;
  userId: string;
  permissions: string[];
  expiresAt?: string;
}

export default function VaultDashboard() {
  const [tabValue, setTabValue] = useState(0);
  const [assets, setAssets] = useState<Asset[]>([]);
  const [accessRules, setAccessRules] = useState<AccessRule[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [uploadDialog, setUploadDialog] = useState(false);
  const [shareDialog, setShareDialog] = useState(false);
  const [selectedAsset, setSelectedAsset] = useState<Asset | null>(null);

  useEffect(() => {
    loadAssets();
    loadAccessRules();
  }, []);

  const loadAssets = async () => {
    try {
      const response = await axios.get('/api/vault/assets');
      setAssets(response.data);
    } catch (err) {
      setError('Failed to load assets');
    } finally {
      setLoading(false);
    }
  };

  const loadAccessRules = async () => {
    try {
      const response = await axios.get('/api/vault/access-rules');
      setAccessRules(response.data);
    } catch (err) {
      console.error('Failed to load access rules');
    }
  };

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleUpload = async (file: File) => {
    const formData = new FormData();
    formData.append('file', file);

    try {
      await axios.post('/api/vault/assets', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      loadAssets();
      setUploadDialog(false);
    } catch (err) {
      setError('Failed to upload asset');
    }
  };

  const handleShare = async (assetId: string, userId: string, permissions: string[]) => {
    try {
      await axios.post('/api/vault/access-rules', {
        assetId,
        userId,
        permissions
      });
      loadAccessRules();
      setShareDialog(false);
    } catch (err) {
      setError('Failed to share asset');
    }
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      {/* Header */}
      <AppBar position="static" color="primary">
        <Toolbar>
          <SecurityIcon sx={{ mr: 2 }} />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Azora Vault - Secure Asset Management
          </Typography>
          <IconButton color="inherit">
            <SettingsIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Container maxWidth="xl" sx={{ mt: 4, mb: 4 }}>
        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        {/* Status Cards */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Total Assets
                </Typography>
                <Typography variant="h5">
                  {assets.length}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Encrypted Assets
                </Typography>
                <Typography variant="h5">
                  {assets.filter(a => a.encrypted).length}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Shared Assets
                </Typography>
                <Typography variant="h5">
                  {assets.filter(a => a.shared).length}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Access Rules
                </Typography>
                <Typography variant="h5">
                  {accessRules.length}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        {/* Tabs */}
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={tabValue} onChange={handleTabChange} aria-label="vault tabs">
            <Tab label="Assets" />
            <Tab label="Access Control" />
            <Tab label="Credentials" />
          </Tabs>
        </Box>

        {/* Assets Tab */}
        {tabValue === 0 && (
          <Box sx={{ mt: 3 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
              <Typography variant="h6">Your Assets</Typography>
              <Button
                variant="contained"
                startIcon={<UploadIcon />}
                onClick={() => setUploadDialog(true)}
              >
                Upload Asset
              </Button>
            </Box>

            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Name</TableCell>
                    <TableCell>Type</TableCell>
                    <TableCell>Size</TableCell>
                    <TableCell>Created</TableCell>
                    <TableCell>Status</TableCell>
                    <TableCell>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {assets.map((asset) => (
                    <TableRow key={asset.id}>
                      <TableCell>
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <FolderIcon sx={{ mr: 1 }} />
                          {asset.name}
                        </Box>
                      </TableCell>
                      <TableCell>{asset.type}</TableCell>
                      <TableCell>{(asset.size / 1024).toFixed(1)} KB</TableCell>
                      <TableCell>{new Date(asset.createdAt).toLocaleDateString()}</TableCell>
                      <TableCell>
                        {asset.encrypted && <Chip label="Encrypted" color="primary" size="small" sx={{ mr: 1 }} />}
                        {asset.shared && <Chip label="Shared" color="secondary" size="small" />}
                      </TableCell>
                      <TableCell>
                        <IconButton
                          onClick={() => {
                            setSelectedAsset(asset);
                            setShareDialog(true);
                          }}
                        >
                          <ShareIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

        {/* Access Control Tab */}
        {tabValue === 1 && (
          <Box sx={{ mt: 3 }}>
            <Typography variant="h6" sx={{ mb: 2 }}>Access Rules</Typography>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Asset</TableCell>
                    <TableCell>User</TableCell>
                    <TableCell>Permissions</TableCell>
                    <TableCell>Expires</TableCell>
                    <TableCell>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {accessRules.map((rule) => (
                    <TableRow key={rule.id}>
                      <TableCell>{rule.assetId}</TableCell>
                      <TableCell>{rule.userId}</TableCell>
                      <TableCell>
                        {rule.permissions.map(perm => (
                          <Chip key={perm} label={perm} size="small" sx={{ mr: 0.5 }} />
                        ))}
                      </TableCell>
                      <TableCell>
                        {rule.expiresAt ? new Date(rule.expiresAt).toLocaleDateString() : 'Never'}
                      </TableCell>
                      <TableCell>
                        <Button size="small" color="error">Revoke</Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

        {/* Credentials Tab */}
        {tabValue === 2 && (
          <Box sx={{ mt: 3 }}>
            <Typography variant="h6" sx={{ mb: 2 }}>Stored Credentials</Typography>
            <Alert severity="info">
              Credential vault functionality coming soon. This will securely store and manage digital credentials issued by Azora services.
            </Alert>
          </Box>
        )}
      </Container>

      {/* Upload Dialog */}
      <Dialog open={uploadDialog} onClose={() => setUploadDialog(false)}>
        <DialogTitle>Upload New Asset</DialogTitle>
        <DialogContent>
          <input
            type="file"
            onChange={(e) => {
              const file = e.target.files?.[0];
              if (file) handleUpload(file);
            }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setUploadDialog(false)}>Cancel</Button>
        </DialogActions>
      </Dialog>

      {/* Share Dialog */}
      <Dialog open={shareDialog} onClose={() => setShareDialog(false)}>
        <DialogTitle>Share Asset</DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            label="User ID"
            margin="normal"
          />
          <FormControl fullWidth margin="normal">
            <InputLabel>Permissions</InputLabel>
            <Select multiple>
              <MenuItem value="read">Read</MenuItem>
              <MenuItem value="write">Write</MenuItem>
              <MenuItem value="delete">Delete</MenuItem>
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShareDialog(false)}>Cancel</Button>
          <Button variant="contained">Share</Button>
        </DialogActions>
      </Dialog>

      {/* Footer */}
      <Box sx={{ bgcolor: 'primary.main', color: 'white', py: 2, mt: 4 }}>
        <Container maxWidth="xl">
          <Typography variant="body2" textAlign="center">
            Azora Vault - Secure, programmable asset custody. Azora OS is committed to constitutional compliance.
          </Typography>
        </Container>
      </Box>
    </Box>
  );
}
